:root {
  --colorPrimary: #d41443;
  --colorBlack: #000;
  --colorWhite: #fff;
  --colorLine: #e1e1e1;
  --colorOverline: #ffa5ba;
  --colorBorder: #e18298;
  --colorDescription: #907e82;
  --fontPrimary: "DM Sans";
