.nav__menu {
  color: var(--colorWhite);
  background-color: rgba(0, 0, 0, 0);
  justify-content: start;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.38;
  display: flex;
}

.nav__item {
  transition: color var(--durationPrimary) var(--timingSecondary);
}

.nav__item + .nav__item {
  margin-left: 35px;
}

.nav__item:hover, .nav__item:focus, .nav__item:active {
  color: var(--colorPrimary);
}

