.header__backdrop-menu {
  visibility: hidden;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  background-color: var(--bgcBackDrop);
  transition: visibility var(--durationPrimary) var(--timingPrimary);
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.header__backdrop-menu.is-open {
  visibility: visible;
}

.header__menu {
  z-index: 20;
  width: var(--menuMobileWidth);
  height: 100vh;
  background-color: var(--bgcMenuMobile);
  box-shadow: var(--shadowMenuMobile);
  opacity: 0;
  transition: transform var(--durationSecondary) var(--timingSecondary), opacity var(--durationSecondary) var(--timingSecondary);
  position: absolute;
  top: 0;
  left: 100vw;
  overflow: hidden;
  transform: translateX(0);
}

.header__menu.is-open {
  opacity: 1;
  transform: translateX(-100%);
}

.header__menu-close {
  z-index: 20;
  width: 14px;
  height: 14px;
  fill: var(--colorWhite);
  transition: fill var(--durationPrimary) var(--timingPrimary);
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}

.header__menu-close:hover, .header__menu-close:focus {
  fill: var(--colorPrimary);
  scale: 120%;
}

@media screen and (min-width: 1200px) {
