.advantages {
  background-color: var(--bgcSection);
  text-align: left;
}
.advantages__list {
  @media screen and (min-width: $tablet) {
    display: flex;
    gap: 12px;
    text-align: left;
  }
  @media screen and (min-width: $desktop) {
    gap: 90px;
  }
}

// .advantages__item {
//   max-width: 170px;
//   @media screen and (min-width: $desktop) {
//     max-width: 300px;
//   }
// }

.advantages__item:not(:last-child) {
  @media screen and (max-width: 767px) {
    margin-bottom: 32px;
  }
}

.advantages__title {
  font-family: var(--fontSecondary);
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 24px;
  color: var(--colorPrimary);
  @media screen and (min-width: $tablet) {
    font-size: 48px;
    margin-bottom: 12px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 16px;
    font-size: 58px;
  }
}

.advantages__subtitle {
  font-family: var(--fontPrimary);
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.7;
  color: var(--colorBlack);
  @media screen and (min-width: $tablet) {
    width: 170px;
    font-size: 14px;
  }
  @media screen and (min-width: $desktop) {
    width: 270px;
    font-size: 16px;
  }
}

.advantages__icon1::before {
  content: '';
  display: block;
  background-image: url(/src/images/png/bucket-cow.png);
  background-repeat: no-repeat;
  width: 100%;
  height: 50px;
  margin-bottom: 16px;
  @media screen and (min-width: $tablet) {
    margin-bottom: 36px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 40px;
  }
}

