.products__label{
    margin-bottom: 15px;
    font-family: var(--fontSecondary);
    font-size: 18px;
    line-height: 1.17;
    text-transform: uppercase;
    
    color: var(--colorOverline);

    @media screen and (min-width:$tablet) {
    margin-bottom: 18px;
    font-size: 26px;
    line-height: 1.15;
    }

    @media screen and (min-width:$desktop) {
    margin-bottom: 16px;
    font-size: 30px;
    ine-height: 1.13;
        }

}
.products__title{
    margin-bottom: 8px;
    font-family: var(--fontSecondary);
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    
    color: var(--colorPrimary);

    @media screen and (min-width:$tablet) {
    margin-bottom: 20px;
    font-size: 48px;
    
        }
    
    @media screen and (min-width:$desktop) {
    margin-bottom: 16px;
    font-size: 58px;
    letter-spacing: 0.06em;
        }
}

.products-list{
    @media screen and (min-width:$tablet) {
            display: flex;
            gap: 16px;
        }

    @media screen and (min-width:$desktop) {
     gap: 32px;
}

.products-list__item{
    // position: relative;
    width: 100%;
    text-align: left;
    perspective: 1500px;
    margin-top: 140px;
    max-width: 480px;
    height: 489px;
    border-radius: 24px;



