.product-card__title{
   
    padding-top: 160px;
    margin-top: 20px;
    margin-bottom: 32px;
    font-family: var(--fontSecondary);
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #FFFFFF;


    @media screen and (min-width:$tablet) {
    margin-top: 32px;
    padding-top: 86px;
    }

    @media screen and (min-width:$desktop) {
    font-size: 30px;
    padding-top: 133px;
        }

    &::after{
    display: flex;
   margin-right: auto;
   margin-left: auto;
   margin-top: 32px;
    content: "";
    width: 26px;
    height: 6px;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url(../images/products/product-dot.png);
    @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/products/product-dot@2x.png);
        };
    }
}
.product-card{
    display: flex;
    flex-direction: column;
    
    padding-left: 41px;
    padding-right: 39px;

@media screen and (min-width:($tablet)) {
    padding-left: 32px;
    padding-right: 32px;
    }

@media screen and (min-width:($desktop)) {
    padding-left: 64px;
    padding-right: 63px;
        }
    }
.front,
.back {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    display: flex;
    top: 0;
    transition: transform 500ms linear;
    backface-visibility: hidden;
    
        }
    
 .back {
    transform: rotateY(180deg);
        };

.products-list__wraper.is-flipped .front {
    transform: rotateY(180deg);
}

.products-list__wraper.is-flipped .back {
    transform: rotateY(360deg);
}

.product-card{
    &__item{
    display: flex;
    padding: 7px 0;
    justify-content: space-between;
    text-align: left;
    }

    &__name{
    margin: 0;
    font-size: 18px;
    color: var(--colorPrimary);
    }

    &__price{
    font-size: 20px;
    color: var(--bgcBtnWhite);
    }

    &__text{
font-family: var(--fontPrimary);
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    letter-spacing: 0.04em;

    color: #FFFFFF;

    @media screen and (min-width:($tablet)) {
        line-height: 1.76;
    }

    @media screen and (min-width:($desktop)) {
        font-size: 16px;
    }
    }
}


.product-card__text-mobile{
    @media screen and (min-width:$tablet) {
        display: none;
    }
}

.product-card__btn {
    @include flex-pos ();
    margin-top: 32px;
    margin-right: auto;
    margin-left: auto;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    outline: none;
    background-color: var(--bgcBtnWhite);

    @media screen and (min-width:($desktop)) {
    margin-top: 60px;
        }

}

.product-card__icon{
  fill: var(--colorPrimary);
}

